import { Row, Col } from "antd";
import { withTranslation, TFunction } from "react-i18next";
import { Slide } from "react-awesome-reveal";
import { Button } from "../../common/Button";
import { PaymentSection, Content, ContentWrapper } from './styles'
import { Title } from "../Footer/styles";

interface PaymentProps {
  title: string;
  content: string;
  button: string;
  t: TFunction;
  id:string;
}
         

const Payment = ({ 
  title, 
  content, 
  button, 
  t, 
  id, 
}: PaymentProps) => { 
  const scrollTo = (id: string) => { 
    const element = document.getElementById(id) as HTMLDivElement; 
    element?.scrollIntoView({ behavior: "smooth" }); 
  }; // Remove the misplaced closing bracket here!!

  return ( 
    
    <PaymentSection aria-label="payments">
      
      <Slide direction="up" triggerOnce>
        <Row justify="center" align="middle">
          <ContentWrapper>
            <Col lg={24} md={24} sm={24} xs={24}>
              <Title>Payments</Title>
            </Col>
            <Col lg={24} md={24} sm={24} xs={24}>
              <Button color="primary" href="https://buy.stripe.com/test_cNifZj7et2XAbKG6p4aZi00">
                Make A Payment (Less Than 30 Minutes) $50
              </Button>
            </Col>
            <Col lg={24} md={24} sm={24} xs={24}>
              <Button color="primary" href="https://buy.stripe.com/test_5kQ7sNcyNcyabKG14KaZi01">
                Make A Payment (More than 30 Minutes Less Than 1 Hour) $80 to $100
              </Button>
            </Col>
            <Col lg={24} md={24} sm={24} xs={24}>
              <Button color="primary" href="https://buy.stripe.com/test_4gM9AVfKZ69M5mi9BgaZi02">
                Make A Payment (More Than 1 Hour) $120 to $180
              </Button>
            </Col>
          </ContentWrapper>
        </Row>
      </Slide>
    </PaymentSection>
  ); 
}; 

export default withTranslation()(Payment);

